.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop {
  z-index: 3;
  background-color: var(--violet);
  transition: opacity var(--transition), visibility var(--transition);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.modal {
  transition: transform var(--transition);
  background-color: var(--light-bg);
  border: 2px solid var(--black);
  border-radius: 18px;
  max-width: 335px;
  padding: 40px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-close {
  transition: scale var(--transition);
  background-color: #0000;
  border: none;
  place-items: center;
  width: 20px;
  height: 20px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-close:is(:hover, :focus) .close-icon {
  stroke: var(--violet);
}

.close-icon {
  stroke: var(--black);
  transition: stroke var(--transition);
  width: 20px;
  height: 20px;
}

.authorization-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.authorization-input {
  border: 2px solid var(--black);
  font-family: var(--font-family);
  letter-spacing: -.16px;
  border-radius: 40px;
  width: 295px;
  padding: 16px 24px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.13;
}

.authorization-input:hover, .authorization-input:focus, .authorization-input:active {
  border: 2px solid var(--violet);
}

.input-email-wrapper {
  margin: 24px 0;
  position: relative;
}

.input-password-wrapper {
  position: relative;
}

.svg-email, .svg-password {
  fill: none;
  stroke: var(--black);
  width: 18px;
  height: 18px;
  position: absolute;
  top: 16px;
  right: 44px;
}

.signup-btn {
  font-family: var(--font-family);
  letter-spacing: -.16px;
  text-transform: uppercase;
  background-color: var(--black);
  color: var(--white);
  transition: background-color (--transition), color var (--transition);
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  width: 294px;
  margin-top: 24px;
  margin-bottom: 14px;
  padding: 16px;
  font-size: 16px;
  line-height: 1.13;
  display: flex;
}

.signup-btn:hover, .signup-btn:focus {
  background-color: var(--light-bg);
  color: var(--black-1);
}

.authorization-input::placeholder {
  color: var(--black);
  letter-spacing: -.18px;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.33;
}

.link-wrapper {
  gap: 20px;
  display: flex;
}

.sign-link {
  font-family: var(--font-family);
  letter-spacing: -.14px;
  text-transform: uppercase;
  transition: box-shadow var(--transition);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.sign-link:is(:hover, :focus) {
  box-shadow: 0 3px 3px #00000026;
}

.sign-up-link {
  color: var(--violet);
  text-decoration-line: underline;
}

.sign-in-link {
  color: #11111180;
}

